button {
  font-family: 'Titillium Web', sans-serif !important;
}

.button-div {
  padding-top: 30%;
  position: absolute;
  /* width:100% */

}
.SearchButton {
  background-color: #2979ff;
  color: white;
  cursor: pointer;
  display: block;
  font-size:20px;
  font-weight: 400;
  line-height: 45px;
  margin: 0 auto 2em;
  max-width: 160px;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  vertical-align: middle;
  width: 100%;
  border: 0 solid;
  -webkit-box-shadow: 0 0 20px white;
          box-shadow: 0 0 20px white;
  outline: 1px solid;
  outline-color: white;
  outline-offset: 0px;
  text-shadow: none;
  -webkit-transition: all 1250ms cubic-bezier(0.19, 1, 0.22, 1);
  -o-transition: all 1250ms cubic-bezier(0.19, 1, 0.22, 1);
  transition: all 1250ms cubic-bezier(0.19, 1, 0.22, 1);
}

.SearchButton:hover {
  border-color: inherit;
  border: none;
  background-color: #004ecb;
  /* box-shadow: inset 0 0 20px white, 0 0 20px white; */
  outline-color: white;
  outline-offset: 15px;
  /* text-shadow: 1px 1px 2px white; */
}

.container-fluid {
  margin: 0 auto;
  width: 100%;
  height: 100%
}

.location{
  text-align: center;
}



